body {
  background-color: #1a0000;
  color: #ff4d4d;
  font-family: monospace;
  font-size: 24px;
}

.wrapper {
  // display: grid;
  // grid-template-areas:
  //   'quote chat'
  //   'cube chat';
  // grid-template-columns: 1fr 1fr;
  // grid-template-rows: 20em 40em;
  // grid-gap: 1em;
  height: calc(100vh - 2em);
  margin: 1em;
}

.quote {
  grid-area: quote;
}

.chat {
  grid-area: chat;
  display: grid;
  grid-template-areas:
    'sent'
    'textbox';
  grid-template-rows: 9fr 1fr;
}

.chat-sent {
  display: grid;
  grid-area: sent;
  grid-auto-rows: min-content;
}

.chat-textbox {
  grid-area: textbox;
  display: grid;
  grid-gap: 1em;
  grid-template-areas: 'textarea send';
  grid-template-columns: 1fr 6em;
}

.chat-textarea {
  grid-area: textarea;
  background-color: transparent;
  color: #ff4d4d;
  resize: none;
  width: 100%;
  height: 100%;
}

.btn {
  background-color: transparent;
}

.chat-send-btn {
  grid-area: send;
  height: 100%;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.message {
  width: 100%;
  grid-gap: 1em;
  clear: both;
  padding-bottom: 1em;
}

.message-outgoing {
  display: grid;
  grid-template-areas:
    'message-avatar message-text';
  grid-template-columns: 4em 1fr;
}

.message-incoming {
  display: grid;
  grid-template-areas:
    'message-text message-avatar';
  grid-template-columns: 1fr 4em;
}

.message-avatar {
  border-radius: 50%;
  height: 4em;
  width: 4em;
  text-align: center;
}

.cube {
  grid-area: cube;
}

.ui-box {
  border: solid 3px #ff4d4d;
  animation: boxShadow 1.6s infinite;
  padding: 1em;
}

